@keyframes progress-bar-stripes {
  0% {
    background-position: 40px 0;
  }
  to {
    background-position: 0 0;
  }
}
[data-progress] {
  animation: progress-bar-stripes 2s linear infinite;
}

[data-fade-in] {
  transform: translateY(20px);
  opacity: 0;
  animation: fadeIn 0.5s ease 1 forwards;
}

@keyframes fadeIn {
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pulsate {
  0% {
    transform: scale(0.6, 0.6);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
[data-pulsate] {
  animation: pulsate 1.6s ease-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
[data-pulsate] {
  animation: pulsate 1.6s ease-out infinite;
}

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  85% {
    opacity: 0.6;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
[data-pulsate] {
  animation: pulsate 1.6s ease-out infinite;
}

.selectbox_wrapper {
  line-height: 1.25;
  position: relative;
  cursor: pointer;
}
.selectbox_input {
  min-height: 40px;
  padding-top: 13px;
  padding-bottom: 14px;
  padding-left: 15px;
  padding-right: 63px;
  z-index: 1;
  position: relative;
  font-size: 12px;
  font-weight: 600;
  color: var(--color-black);
  border: none;
  background-color: var(--color-white);
  border-radius: 22px;
  box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.43);
}
@media (min-width: 1400px) {
  .selectbox_input {
    min-width: 234px;
  }
}
.selectbox_input_value {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  display: block;
}
.selectbox_input_arrow {
  font-size: 7px;
  position: absolute;
  right: 13px;
  top: 50%;
  transform: translateY(-50%);
  margin-top: -1px;
}
.selectbox_input_arrow i {
  transition: all 0.2s ease;
}
.selectbox_list {
  background-color: var(--color-white);
  border-radius: 22px;
  box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.43);
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 6px;
  list-style-type: none;
  padding: 0;
  width: 100%;
  overflow-y: auto;
  max-height: 160px;
  min-width: 80px;
  z-index: 2;
}
.selectbox_list_item {
  transition: all 0.2s ease;
  padding-top: 10px;
  padding-bottom: 9px;
  padding-left: 18px;
  padding-right: 9px;
  border-bottom: 1px solid #E4E4E4;
}
.selectbox_list_item:last-child {
  border-bottom-width: 0;
}
.selectbox_list_item:hover {
  opacity: 0.6;
}
.selectbox_list_item.selected {
  color: var(--color-accent);
  opacity: 1;
}
.selectbox_list_item.disabled {
  opacity: 0.5;
}
.selectbox_open .selectbox_list {
  display: block;
}
.selectbox_open .selectbox_input_arrow i {
  transform: rotate(-180deg);
}
.selectbox_direction_bottom .selectbox_list {
  top: inherit;
  bottom: 100%;
}