.selectbox {
	&_wrapper {
		line-height: 1.25;
		position: relative;
		cursor: pointer;
	}
	&_input{
		min-height: 40px;
		padding-top: 13px;
		padding-bottom: 14px;
		padding-left: 15px;
		padding-right: 63px;
		z-index: 1;
		position: relative;
		font-size: 12px;
		font-weight: 600;
		color: var(--color-black);
		border: none;
		background-color: var(--color-white);
		border-radius: 22px;
		box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.43);

		@include xxl-min {
			min-width: 234px;
		}

		&_value{
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			width: 100%;
			display: block;
		}
		&_arrow {
			font-size: 7px;
			position: absolute;
			right: 13px;
			top: 50%;
			transform: translateY(-50%);
			margin-top: -1px;
			i {
				transition: all .2s ease;
			}
		}
	}
	&_list{
		background-color: var(--color-white);
		border-radius: 22px;
		box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.43);
		display: none;
		position: absolute;
		left: 0;
		top: 100%;
		margin-top: 6px;
		list-style-type: none;
		padding: 0;
		width: 100%;
		overflow-y: auto;
		max-height: 160px;
		min-width: 80px;
		z-index: 2;

		&_item{
			transition: all .2s ease;
			padding-top: 10px;
			padding-bottom: 9px;
			padding-left: 18px;
			padding-right: 9px;
			border-bottom: 1px solid #E4E4E4;
			&:last-child{
				border-bottom-width:0;
			}
			&:hover{
				opacity: .6;
			}
			&.selected{
				color: var(--color-accent);
				opacity: 1;
			}
			&.disabled{
				opacity: 0.5;
			}
		}
	}
	&_open{
		.selectbox{
			&_list{
				display: block;
			}
			&_input{
				&_arrow{
					i{
						transform: rotate(-180deg);
					}
				}
			}
		}
	}
	&_direction_bottom{
		.selectbox{
			&_list{
				top: inherit;
				bottom: 100%;
			}
		}
	}
}