.r-slider{
    background-color: rgba(52, 84, 81, .6);
    box-shadow: inset 0 4px 4px rgba(0, 0, 0, .25);
    border-radius: 5px;
    height: 10px;
    position: relative;
    cursor: pointer;
    margin: 13px 0;
    width: 100%;
    user-select: none;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0)
}
.r-slider *{
    user-select: none;
    box-shadow: none;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0)
}
.r-slider_hidden{
    visibility: hidden
}
.r-slider__cloud{
    user-select: none;
    position: absolute;
    top: -40px;
    padding: 3px 5px;
    height: 14px;
    background-color: #f0f0f0;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #777;
    font-weight: bold;
    width: max-content
}
.r-slider__cloud_moving{
    top: -34px;
    background-color: #526a83;
    color: #fff;
    z-index: 1
}
.r-slider__cloud_moving::before{
    position: absolute;
    display: block;
    content: "";
    bottom: -6px;
    left: 50%;
    width: 0;
    height: 0;
    margin-left: -3px;
    overflow: hidden;
    border: 3px solid rgba(0,0,0,0);
    border-top-color: #6883a0
}
.r-slider__cloud_right{
    right: 0
}
.r-slider__circle{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 22px;
    height: 22px;
    border-radius:  50%;
    background-color: #fff;
    border: 1px solid #6CAC39;
    cursor: pointer;
    background-color: #77BA3F;
    touch-action: none;
    transition: all .3s ease;
}
.r-slider__filled-line{
    background:  linear-gradient(-184deg, #9DDE3C 0%, #70B340 100%);
    border-radius: 5px;
    height: 10px;
    width: 0;
    transition: all .3s ease;
}

.r-slider--primary .r-slider__cloud {
    display:  none;
}