.--before,
.--after {
    position: relative;
    z-index: 1;
}

.--before::before,
.--after::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    border-radius: inherit;
    z-index: -1;
    background-size: cover !important;
    transition: all .3s ease;
}

.--before-fluid::before,
.p-abs-around--fluid {
    width: 100%;
    height: 100%;
}

.p-abs-around,
.p-abs-around--before::before,
.p-abs-around--after::after,
.p-fix-around--fluid {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -2;
  border-radius: inherit;
}

.p-fix-around--fluid {
    position: fixed;
}

.p-abs-around--before::before,
.p-abs-around--after::after {
  content: '';
}

