      & svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: rotate(90deg);
  
        & path {
          stroke-width: 0.2em;
          stroke-dasharray: 154;
          stroke-dashoffset: calc(-154px + var(--scrollAmount));
          animation-fill-mode: forwards;
          stroke: var(--color-accent);
        }
      }
    }
}

