// modal
.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    outline: 0;
    display: none;
    opacity: 0;
    transition: opacity .3s ease;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 15px;
}

.modal.open {
    opacity: 1;
}

.modal__dialog {
    position: relative;
    width: auto;
    margin: .5rem;
    pointer-events: none;
    transition: -webkit-transform .3s ease-out;
    transform: translate(0, -25%);
}

.modal__dialog--centered {
    display: flex;
    align-items: center;
    min-height: calc(100% - 30px);
    margin: auto;
}

.modal__dialog--544 {
    max-width: 544px;
}

.modal.open .modal__dialog {
    transform: translate(0, 0);
}

.modal__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border-radius: 50px;
    outline: 0;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
}

.modal__header {
    padding: 57px 40px 0 40px;
