// content

.content * {
    margin: revert;
    padding: revert;
}

.content > * {
    margin-top: 20px;
    margin-bottom: 0;
}

.content > *:first-child {
    margin-top: 0;
}

.content {
    color: var(--color-black);
    font-size: 16px;
    line-height: tolhp(16, 117.7);

    & > img {
        border-radius: 8px;
        vertical-align: middle;
        margin-bottom: 20px;
    }

    h1, h2, h3, h4, h5, h6 {
        font-family: 'BeaufortforLOL', sans-serif;
        font-weight: 700;
    }

    h1 {
        font-size: toEm(21.35, 16);
    }

    h2 {
        font-size: toEm(18, 16);
    }

    h3 {
        font-size: toEm(16, 16);
    }

    h4 {
        font-size: toEm(15, 16);
    }

    h5 {
        font-size: toEm(14, 16);
    }

    h6 {
        font-size: toEm(13, 16);
    }

    h3 + * {
        margin-top: 10px;
    }

    ul + ul {
        margin-top: 28px;
    }
    
    a {
        color: #e4d2aa;
        text-decoration: underline;
    }
    
    a:hover {
        color: var(--color-white);
        text-decoration: none;
    }
}

.content {
    table {
        min-width: 800px;
        font-size: 14px;
        border-spacing: 2px;
        width: 100%;
        table-layout: fixed;

        th {
            font-weight: 600;
            font-size: 11px;
            text-align: left;
            color: #e4d2aa;
            padding: 9px 17px;
            background-color: #554c3e;
            border-radius: 2px;
            height: 38px;
        }

        td {
            padding: 9px 17px;
            background-color: #2d2922;
            border-radius: 2px;
            height: 38px;
        }

        tr:nth-child(odd) td {
            background-color: #15110f;
        }
    }
}

.list {
    padding-left: 0;
    list-style: none;

    li {
        position: relative;
        z-index: 1;
    }

    li::before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
    }
}

.list-1,
.list-2 {
    li::before {
        background-color: #f6f6f6;
        transform: rotate(45deg);
    }
}

.list-1 li {
    padding-left: toEm(20, 16);

    & + li {
        margin-top: toEm(20, 16);
    }

    &::before {
        height: toEm(5, 16);
        width: toEm(5, 16);
        margin-top: toEm(10, 16);
    }
}

.content.content--modal {

    h1 {
        font-size: toEm(31.35, 16);
    }

    h2 {
        font-size: toEm(28, 16);
    }

    h3 {
        font-size: toEm(26, 16);
    }

    h4 {
        font-size: toEm(25, 16);
    }

    h5 {
        font-size: toEm(24, 16);
    }

    h6 {
        font-size: toEm(23, 16);
    }
    
    * {
        line-height: inherit;
    }
}