.ArgentumSans {
  font-family: "ArgentumSans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}

.Caveat {
  font-family: "Caveat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}

.Montserrat {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}

.wh-nrm {
  white-space: normal;
}

.wh-nwp {
  white-space: nowrap;
}

.wb-nrm {
  word-break: normal;
}

.wb-ba {
  word-break: break-all;
}

.wb-bw {
  word-break: break-word;
}

.vrta-t {
  vertical-align: top;
}

.vrta-m {
  vertical-align: middle;
}

.vrta-b {
  vertical-align: bottom;
}

.vrta-bs {
  vertical-align: baseline;
}

.to-els {
  text-overflow: ellipsis;
  overflow: hidden;
}

.tt-upp {
  text-transform: uppercase;
}

.tt-low {
  text-transform: lowercase;
}

.tt-cap {
  text-transform: capitalize;
}

.td-un, .td-un-h:hover {
  text-decoration: underline;
}

.ls-inh {
  letter-spacing: inherit;
}

.fts-inh {
  font-size: inherit;
}

.lh-inh {
  line-height: inherit;
}

.ftstl-nrm {
  font-style: normal;
}
  
.ftw-100, .ftw-100-h:hover {
  font-weight: 100;
}

.ftw-200, .ftw-200-h:hover {
  font-weight: 200;
}

.ftw-300, .ftw-300-h:hover {
  font-weight: 300;
}

.ftw-350, .ftw-350-h:hover {
  font-weight: 350;
}

.ftw-400, .ftw-400-h:hover {
  font-weight: 400;
}

.ftw-500, .ftw-500-h:hover {
  font-weight: 500;
}

.ftw-600, .ftw-600-h:hover {
  font-weight: 600;
}

.ftw-700, .ftw-700-h:hover {
  font-weight: 700;
}

.ftw-800, .ftw-800-h:hover {
  font-weight: 800;
}

.ftw-900, .ftw-900-h:hover {
  font-weight: 900;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

@include lg-min {

  .text-lg-left {
    text-align: left;
  }

  .text-lg-center {
    text-align: center;
  }

  .text-lg-right {
    text-align: right;
  }

  .text-lg-justify {
    text-align: justify;
  }

  .ftw-lg-100 {
    font-weight: 100;
  }
  
  .ftw-lg-200 {
    font-weight: 200;
  }
  
  .ftw-lg-300 {
    font-weight: 300;
  }
  
  .ftw-lg-350 {
    font-weight: 350;
  }
  
  .ftw-lg-400 {
    font-weight: 400;
  }
  
  .ftw-lg-500 {
    font-weight: 500;
  }
  
  .ftw-lg-600 {
    font-weight: 600;
  }
  
  .ftw-lg-700 {
    font-weight: 700;
  }
  
  .ftw-lg-800 {
    font-weight: 800;
  }
  
  .ftw-lg-900 {
    font-weight: 900;
  }
}